.footer {
  display: flex;
  height: 50px;
  flex-direction: row;
  margin: 0;
  padding: 0;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $screen-s) {
     height: 80px;
  }

  @media (min-width: $screen-m) {
    height: 50px;
  }

  @media (min-width: $screen-l) {
    height: 50px;
  }

  &__inner {
    display: flex;
    margin: 0 auto;
    padding: 0;
    justify-content: space-between;

    @media (min-width: $screen-s) {
      width: 300px;
    }

    @media (min-width: $screen-m) {
      width: 680px;
      display: flex;
      flex-direction: row;
      align-items: flex-end;
    }

    @media (min-width: $screen-l) {
      width: 1160px;
      display: flex;
      flex-direction: row;
    }
  }

  &__inner-logo {
    margin: 0;
    padding: 0;

    @media (min-width: $screen-s) {
      width: 125px;
      padding-left: 0;
    }

    @media (min-width: $screen-m) {
      padding-left: 0;
    }

    @media (min-width: $screen-l) {
      padding-left: 30px;
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &__inner-worktime {
    margin: 0;
    font-weight: 400;
    font-size: $font-size-xs;
    color: #d5d5d5;

    @media (min-width: $screen-s) {
      padding-left: 0;
      padding-top: 10px;
    }

    @media (min-width: $screen-m) {
      padding-left: 0;
    }

    @media (min-width: $screen-l) {
      padding-bottom: 20px;
      padding-right: 450px;
    }
  }
}

