.main-nav {
  display: flex;
  padding: 0;

  &.hide {
    display: none;

    @media (min-width: 760px) {
      display: flex;
    }
  }

  @media (min-width: $screen-l) {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin: 0;
    padding: 0;
  }

  &__list {

    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    font-weight: 700;
    font-size: $font-size-xs;

    @media (min-width: $screen-m) {
      display: flex;
      order: 3;
    }

    @media (min-width: $screen-l) {
      order: 3;
    }
  }

  &__item {
    margin: 0;
    padding: 0;
    padding-left: 20px;
    list-style: none;
    // display: none;
  }
  &__link {
    margin: 0;
    padding: 0;
    text-decoration: none;
    font-weight: 700;
    color: $text-color;
  }

  &__link:hover {
    margin: 0;
    padding: 0;
    text-decoration: none;
    color: $pink;
    font-weight: 700;
  }

  &__toggle {
    font-size: 0;
    position: relative;

    &,
    &:before,
    &:after {
      width: 30px;
      height: 3px;
      background-color: #000;
      transition: 0.2s ease-in-out;
      border-radius: 5px;
    }

    &:before,
    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      will-change: transform;
    }

    &:before {
      transform: translateY(-6px);
    }

    &:after {
      transform: translateY(6px);
    }
  }


}

  .main-nav__toggle.is-active {
    transform: rotate(-45deg);

    &:before,
    &:after {
      transform: translateX(0) rotate(-90deg);
    }
  }






